<template>
<div class="wpt-input" :class="{floating: isFloating()}">
  <label v-if="label">{{label}}</label>
  <input ref="field" :type="genre" :id="id"
    @input="input($event)"
    @focus="focus($event)"
    @blur="blur($event)"
    @change="change($event)"
    @mousedown="mousedown($event)"
    @keyup="keyup($event)"
  />
</div>
</template>

<script>
export default {
  name: 'wptInput',
  props: {
    /**
      * 
    */
    label: {
      type: String,
    },
    /**
      * , ex: 'text','number','password','email','tel','date'
    */
    genre: {
      type: String,
      default: 'text',
    },
    /**
      * id
    */
    id: {
      tpye: String,
    },
  },
  data() {
    return {
      isFocus: false,
    };
  },
  methods: {
    isFloating() {
      const field = this.$refs.field;
      const value = field ? field.value : '';
      const focused = this.isFocus;
      const hasLabel = !!this.label;
      return hasLabel && !value && !focused;
    },
    input(event) {
      this.value = event.target.value;
      /**
       * 
       * @type {Event}
       */
      this.$emit('input', event);
    },
    focus(event) {
      this.isFocus = true;
       /**
       * 
       * @type {Event}
       */
      this.$emit('focus', event);
    },
    blur(event) {
      this.isFocus = false;
      /**
       * 
       * @type {Event}
       */
      this.$emit('blur', event);
    },
    change(event) {
      /**
       * 
       * @type {Event}
       */
      this.$emit('change', event);
    },
    keyup(event) {
      /**
       * 
       * @type {Event}
       */
      this.$emit('keyup', event);
    },
    mousedown(event) {
      event.target.focus();
    },
  },
};
</script>

<style lang="scss">
@import '~@/stories/assets/css/addon/main';

.wpt-input {
  overflow: hidden;

  label {
    display: table;
    line-height: 1;
    color: rgba(0, 0, 0, .54);
    pointer-events: none;
    transition:
      color .1s ease,
      transform .2s ease,
      -webkit-transform .2s ease;
  }

  input {
    margin: 0;
    padding: 0;
    width: 100%;
    font-size: inherit;
    line-height: 2;
    background: none;
    border: none;
    border-bottom-color: rgba(0,0,0,.12);
    border-bottom-style: solid;
    border-bottom-width: 1px;
    outline: none;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  &.floating {
    label {
      transform: translateX(.15em) translateY(1.5em) scale(1.1);
    }
  }

  &.error {
    label, input {
      color: $error;
    }
  }

  &.success {
    label, input {
      color: $success;
    }
  }
}
</style>